import { useState } from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  height: 60vw;
  max-height: 300px;
  display: flex;
  align-items: center;
`;

const TeapotIcon = styled.div`
  width: 40vw;
  max-width: 200px;
  height: 100%;
  border-radius: 5px 5px 20px 20px;
  border: 4px solid black;
  border-top: none;
  display: flex;
  align-items: flex-end;
  justify-content: center;
`;

const TeapotWater = styled.div`
  width: 100%;
  background: rgba(112, 241, 255, 0.4);
  border-radius: 1px 1px 15px 15px;
  height: ${({amount, max}) => (amount/max)*100}%;
  z-index: -1;
  // transition: height 0.2s;
`;

const ScaleWrapper = styled.div`
  height: 110%;
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: space-between;
`;

const ScaleText = styled.div`
  position: relative;
  font-weight: bold;
  white-space: pre-wrap;
`;

const AmountWrapper = styled.div`
  display: flex;
  height: 60vw;
  max-height: 300px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: -2;
  position: absolute;
`;

const TeaLeafAmount = styled.div`
  font-weight: bold;
  font-size 36px;
  margin-bottom: 8px;
`;

const WaterAmount = styled.div`
  font-weight: bold;
  font-size 36px;
  border-top: 4px solid black;
`;

const Teapot = ({
  max,
  amountOfWater,
  amountOfTeaLeaf,
  onClick,
}) => {
  const onClickIcon = (e) => {
    console.log(e);
    if ((e.type === 'mousemove' || e.type === 'touchmove') && e.buttons === 0) {
      return;
    }
    const elementHeight = e.target.offsetHeight;
    const yPos = (e.clientY || e.changedTouches[0].clientY) - e.target.offsetTop;
    if (yPos <= 4) {
      onClick(max);
    } else if (yPos >= elementHeight - 4) {
      onClick(0);
    } else {
      const rate = 1 - (yPos / elementHeight);
      const value = Math.round((rate * max) / 10) * 10;
      onClick(value);
    }
  }

  return (
    <Wrapper>
      <TeapotIcon
        onClick={onClickIcon}
        onMouseMove={onClickIcon}
        onTouchMove={onClickIcon}
      >
        <TeapotWater max={parseInt(max)} amount={parseInt(amountOfWater)} />
        <AmountWrapper>
          <TeaLeafAmount>{isNaN(amountOfTeaLeaf) || !amountOfTeaLeaf ? ' - ' : Math.round(amountOfTeaLeaf * 10) / 10}g</TeaLeafAmount>
          <WaterAmount>{amountOfWater}mL</WaterAmount>
        </AmountWrapper>
      </TeapotIcon>
      <ScaleWrapper>
          <ScaleText>{max||100}</ScaleText>
          <ScaleText>{(max||100)/4*3}</ScaleText>
          <ScaleText>{(max||100)/2}</ScaleText>
          <ScaleText>{(max||100)/4*1}</ScaleText>
          <ScaleText> 0</ScaleText>
        </ScaleWrapper>
    </Wrapper>
  );
};

export default Teapot;