import styled from "styled-components";

const Wrapper = styled.div`
  display: flex;
  margin-left: -30px;
  align-items: center;
`;

const SliderInput = styled.input`
  transform: rotate(-90deg) scale(2);
  width: 30vw;
  max-width: 150px;
`;

const Slider = ({
  min = 0,
  max = 100,
  step = 10,
  value,
  setValue = () => null,
}) => {
  const onChange = (e) => {
    setValue(e.target.value);
  }
  return (
    <Wrapper>
      <SliderInput
        type="range"
        min={min}
        max={max||100}
        value={value}
        step={step}
        onChange={onChange}
      />
    </Wrapper>
  );
};

export default Slider;